.Machine {
    display: flex;
    flex-direction: row;
    width: 80%;
    height: 80%;
}

.Machine-sidebar {
    background: #9575cd;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 30%;
    box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12 px rgba(0, 0, 0, 0.1)
        inset 0 0 25px #7e57c2;
        z-index: 2;
}

.Machine-Title {
    font-size: 4rem;
    margin: 2rem;
    color: white;
    font-weight: 150;
    letter-spacing: 0.6rem;
}

.Machine-Title span {
    font-weight: 700;
    letter-spacing: 0;
}

.Machine-sidebar img {
    width: 50%;
    border-radius: 50%;
    box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.1);
}

.Machine-Jokes {
    height: 90%;
    background: white;
    align-self: center;
    width: 70%;
    overflow: scroll;
    box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.1);
}

.Machine-spinner {
    color: white;
    text-align: center;
}

.Machine-getmore {
    font-size: 1.5rem;
    width: 50%;
    border-radius: 2rem;
    padding: 1rem 2rem;
    color: white;
    margin: 2rem;
    border: none;
    font-weight: 700;
    word-spacing: 0.6rem;
    cursor: pointer;
    transition: 0.8s cubic-bezier(0.2, 1, 0.2, 1);
    outline: none;
    background: linear-gradient(
    135deg,
    rgba(179, 229, 252, 1) 0%,
    rgba(179, 229, 252, 1) 50%,
    rgba(240, 98, 146, 1) 50%,
    rgba(240, 98, 146, 1) 100%
  );
  box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.1);
}

.Machine-getmore:hover {
    transform: translateY(-2px);
    box-shadow: 0 15px 30px rgba(0, 0, 0, 0.25);
}